import { CHANGE_THEME, SPLIT_POPUP, UI } from '../constants/actions';

export const changeTheme = (theme) => ({
  type: CHANGE_THEME,
  payload: { theme },
});

export const splitPopUp = {
  open: () => ({ type: SPLIT_POPUP.OPEN }),
  close: () => ({ type: SPLIT_POPUP.CLOSE }),
};

export const blockUI = () => ({
  type: UI.BLOCK,
});

export const unBlockUI = () => ({
  type: UI.UNBLOCK,
});
