import React, { useEffect } from 'react';
import { connect } from "react-redux";
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import Checkbox from '@material-ui/core/Checkbox';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Divider, Grid, Typography } from '@material-ui/core';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { KeyboardDatePicker } from '@material-ui/pickers';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import Autocomplete from '@material-ui/lab/Autocomplete';

import { monent } from 'moment';

//actions
import {
    addMeetup,
    clearMeetup,
    deleteMeetup,
    mountedMeetupDashboardComponent as mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup
} from '../actions/meetup';

import { getCurrentWeather } from '../actions/weather';

//selectors
import { getMeetup } from '../selectors/meetups';
import { getCurrentLocation } from '../selectors/location';
import { getUsuarios, getUsuariosSlice } from '../selectors/usuarios';
import { getWeather } from '../selectors/weather';


const MeetupDashboard = ({
    meetups,
    usuarios,
    weather,
    location,
    addMeetup,
    deleteMeetup,
    mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup
}) => {

    const useStyles = makeStyles((theme) => ({
        formControl: {
            margin: theme.spacing(2),
            minWidth: 200,
        },
        card: {
            maxWidth: 600
        },
        selectEmpty: {
            marginTop: theme.spacing(2),
        },
        select: {
            width: 200
        },
        grid: {
        },
        button: {
            margin: theme.spacing(1),
        }
    }));

    const classes = useStyles();

    useEffect(() => {
        mounted();
        clearMeetup();
    }, [meetups]);

    const moment = require('moment');

    const [meetup, setMeetup] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        usersAttended: [],
        temp: ""
    });

    const [errors, setErrors] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        temp: "",
        day: 0
    });

    const [selectedUsers, setSelectedUsers] = React.useState([]);
    const [selectedDay, setSelectedDay] = React.useState(0);

    const [open, setOpen] = React.useState(false);

    const handleAddMeetup = () => {

        let validation = validateMeetup();

        if (Object.keys(validation).length === 0) {
            const m = {
                ...meetup,
                users: selectedUsers,
                usersAttended: [],
                countSixPacks: meetups.estimatedCount,
                temp: (weather.daily === undefined) ? "" : Math.trunc(weather.daily[selectedDay].temp.day)
            }
            console.log("add meetup", m)
            addMeetup(m);
            setOpen(false);
        }
        setErrors(validation);

    };

    const handleCalculateEstimatedBeer = () => {

        let validation = validateMeetup();

        if (Object.keys(validation).length === 0) {
            const m = { 
                countPerson: meetup.countPerson, 
                temp: weather.daily[selectedDay].temp.day };
            getEstimatedCount(m);
        }
        setErrors(validation);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const clearMeetup = () => {
        setSelectedUsers([]);
        setMeetup({ name: "", fecha: "", countPerson: "", countSixPacks: "", users: [] });
        setSelectedDay(0);
    };

    const handleOnUserSelectChange = (value) => {
        setSelectedUsers(value === undefined ? [] : value);
    };

    const handleSubscribeMeetup = (value) => {

        const meetupName = value.name;
        const myUserName = usuarios.username;

        subscribeMeetup(meetupName, myUserName);
    };

    const handleIwasHereMeetup = (value) => {

        console.log('handleIwasHereMeetup', value)
        const meetupName = value.name;
        const myUserName = usuarios.username;

        iWasHereMeetup(meetupName, myUserName);
    };

    const handleDeleteMeetup = (meetup) => {
        deleteMeetup(meetup);
    };

    const validateMeetup = () => {

        const validation = {};
        if (meetup.name === undefined || meetup.name.length < 1) {
            validation.name = "Requerido";
        }

        if (meetup.countPerson === undefined || meetup.countPerson.length < 1) {
            validation.countPerson = "Requerido";
        }

        return validation
    }

    return <Container>
        {(usuarios.rol === "ADMIN") ?
            <Button variant="outlined" color="primary" onClick={handleClickOpen} className={classes.button}>
                Crear meetup
        </Button>
            : ""
        }
        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Crear meetup</DialogTitle>
            <DialogContent>
                <form onSubmit={e => e.preventDefault()}>
                    <Grid container spacing={2}>
                        <Grid item sm={6}>
                            <TextField
                                label={"Nombre"}
                                fullWidth
                                name='name'
                                value={meetup.name}
                                onChange={event => setMeetup({ ...meetup, "name": event.target.value })}
                                error={Boolean(errors.name)}
                                helperText={errors.name}
                            />
                        </Grid>
                        <Grid item sm={6}>
                            <FormControl
                                style={{ marginTop: 0 }}
                                error={Boolean(errors.fecha) ? true : false}
                                fullWidth>
                                <InputLabel>{"Fecha"}</InputLabel>
                                <Select
                                    label={"Fecha"}
                                    fullWidth
                                    name='fecha'
                                    value={selectedDay}
                                    onChange={event => setSelectedDay(event.target.value)}
                                    error={Boolean(errors.day)}
                                    helperText={errors.day}>
                                    <MenuItem value={0}>{moment().format("DD/MM/YYYY")}</MenuItem>
                                    <MenuItem value={1}>{moment().add(1, 'day').format("DD/MM/YYYY")}</MenuItem>
                                    <MenuItem value={2}>{moment().add(2, 'day').format("DD/MM/YYYY")}</MenuItem>
                                    <MenuItem value={3}>{moment().add(3, 'day').format("DD/MM/YYYY")}</MenuItem>
                                    <MenuItem value={5}>{moment().add(4, 'day').format("DD/MM/YYYY")}</MenuItem>
                                    <MenuItem value={6}>{moment().add(5, 'day').format("DD/MM/YYYY")}</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item sm={12}>
                            <Autocomplete
                                multiple
                                id="users-selector"
                                value={selectedUsers}
                                options={usuarios.byId}
                                disableCloseOnSelect
                                getOptionLabel={(option) => option.username}
                                renderOption={(option, { selected }) => (
                                    <React.Fragment>
                                        <Checkbox
                                            style={{ marginRight: 8 }}
                                            checked={selected}
                                        />
                                        {option.username}
                                    </React.Fragment>
                                )}
                                onChange={(_, newValue) => handleOnUserSelectChange(newValue)}
                                renderInput={(params) => (
                                    <TextField {...params}
                                        variant="outlined" label="Invitados" placeholder="" />
                                )} />

                        </Grid>
                        <Grid item sm={12}>
                            <TextField
                                label={"Cantidad de personas"}
                                fullWidth
                                name='countPerson'
                                type="number"
                                value={meetup.countPerson}
                                onChange={event => setMeetup({ ...meetup, "countPerson": event.target.value })}
                                error={Boolean(errors.countPerson)}
                                helperText={errors.countPerson}
                            />
                        </Grid>
                        <Grid item sm={4}>
                            <Grid item sm={12}>
                                <Typography variant="subtitle2" gutterBottom>
                                    {location.city}, {location.regionName}, {location.country}
                                </Typography>
                            </Grid>
                            <Grid item sm={12}>

                                <Typography variant="h6">
                                    Temp.
                                    </Typography>

                                <Typography variant="h3">
                                    {weather.daily === undefined ? "" : Math.trunc(weather.daily[selectedDay].temp.day) + "°c"}
                                </Typography>

                            </Grid>
                        </Grid>
                        <Grid item sm={4}>
                            <TextField
                                label={"Six Pack"}
                                fullWidth
                                disabled
                                name='countSixPacks'
                                value={meetups.estimatedCount}
                            />
                        </Grid>
                        <Grid item sm={4}>
                            <Button color="primary" variant="contained" component="span" className={classes.button} onClick={handleCalculateEstimatedBeer} >
                                {"Calcular cajas"}
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary">
                    Cancelar
          </Button>
                <Button onClick={handleClose} color="primary" onClick={handleAddMeetup} >
                    Crear
          </Button>
            </DialogActions>
        </Dialog>
        <Grid container spacing={2} className={classes.grid}>
            {meetups?.byId.map(meetup =>
                <Grid item xs={12} sm={12} md={4} xl={4} key={meetup?.name}>
                    <Card className={classes.card} key={meetup?.name} variant="outlined">
                        <CardActionArea>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="h5">
                                    {meetup?.id} - {meetup?.name}
                                </Typography>
                                <Typography variant="overline" display="block" >
                                    Invitados {meetup?.users.map(u =>
                                    <>
                                        <Chip label={u.username} />
                                    </>
                                )}
                                </Typography>
                                <Typography variant="overline" display="block" >
                                    Estuve? {(meetup?.usersAttended?.find(u => u.username === usuarios.username) != undefined) == true ? "si" : "no"}
                                </Typography>
                                <Typography variant="overline" display="block">
                                    Cantidad de personas {meetup?.countPerson}
                                </Typography>
                                <Typography variant="overline" display="block">
                                    Fecha {meetup?.fecha} - Temp: {meetup?.temp}°c
                                </Typography>
                                {(usuarios.rol === "ADMIN") ? <Typography variant="overline" display="block">
                                    Cantidad Sixpacks {meetup?.countSixPacks}
                                </Typography> : ""}
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            {(usuarios.rol === "ADMIN") ? <Button size="small" color="primary" onClick={() => handleDeleteMeetup(meetup)}>
                                Eliminar
                            </Button> : ""}
                            {/* Mostrar botones solo sino estuve en la meetups */}
                            {(meetup?.users?.find(u => u.username === usuarios.username) != undefined) == true ?
                                "" :
                                <Button size="small" color="primary" onClick={() => handleSubscribeMeetup(meetup)}>
                                    Suscribirme
                                </Button>}

                            {(meetup?.usersAttended?.find(u => u.username === usuarios.username) != undefined) == true ?
                                "" : <Button size="small" color="primary" onClick={() => handleIwasHereMeetup(meetup)}>
                                    Estuve Aquí
                                    </Button>}
                        </CardActions>
                    </Card>
                </Grid>
            )}
        </Grid>
    </Container>
};

const mapStateToProps = state => ({
    meetups: getMeetup(state),
    usuarios: getUsuariosSlice(state),
    weather: getWeather(state),
    location: getCurrentLocation(state),
});

const mapDispatchToProps = {
    addMeetup,
    deleteMeetup,
    mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup
};

export default connect(mapStateToProps, mapDispatchToProps)(MeetupDashboard)