import {
    ADD_MEETUP,
    DELETE_MEETUP,
    CLEAR_MEETUP,
    GET_ESTIMATED_COUNT,
    SUBSCRIBE_MEETUP,
    IWAS_HERE_MEETUP
} from "../constants/actions";

let _id = 0;

const initialState = {
    byId: [],
    estimatedCount: 0
};

const meetup = (state = initialState, action) => {

    switch (action.type) {

        case ADD_MEETUP: {

            const meetup = action.payload;
            const newById = state.byId;
            const index = state.byId.findIndex((e) => e.id === meetup.id);

            if (index === -1) {
                meetup.id = Date.now();
                newById.push(meetup);
            } else {
                newById[index] = meetup;
            }

            return {
                ...state,
                byId: newById
            }
        }
        case DELETE_MEETUP: {

            const meetupId = action.payload;
            const meetups = state.byId.filter(meetup => meetup.id != meetupId);

            return {
                ...state,
                byId: meetups
            };
        }
        case CLEAR_MEETUP: {
            return {
                ...state,
                byId: []
            };
        }
        case GET_ESTIMATED_COUNT: {
            //const count = action.payload.count.count;
            console.log("payload", action.payload)
            const { countPerson, temp } = action.payload;
            let result = 0;
            let countPorPersona = 0;

            if (temp > 20 && temp < 24) {
                countPorPersona = 1;
            } else if (temp < 20) {
                countPorPersona = 0.75;
            } else if (temp > 24) {
                countPorPersona = 2;
            }
            result = (countPorPersona * Math.trunc(countPerson))
            console.log("result",result)

            return {
                ...state,
                estimatedCount: result
            };
        }
        case SUBSCRIBE_MEETUP: {

            const newById = state.byId;
            const meetupName = action.payload.meetupName;
            const username = action.payload.username;

            const m = newById.find(meetup => meetup.name === meetupName);
            const index = m.users?.findIndex(user => user.username === username);

            if (index === -1) {
                newById.find(meetup => meetup.name === meetupName).users?.push({ username: username });
            } else {

            }

            return {
                ...state,
                byId: newById,
            };

        }
        case IWAS_HERE_MEETUP: {

            const newById = state.byId;
            const meetupName = action.payload.meetupName;
            const username = action.payload.username;

            const m = newById.find(meetup => meetup.name === meetupName);
            const index = m.usersAttended?.findIndex(user => user.username === username);

            if (index === -1) {
                newById.find(meetup => meetup.name === meetupName).usersAttended?.push({ username: username });
            } else {

            }

            return {
                ...state,
                byId: newById,
            };

        }

        default: { return state; }
    }
};

export default meetup;