import { api } from './api'
import {
    DELETE_MEETUP,
    CLEAR_MEETUP,
    MEETUP_DASHBOARD_COMPONENT,
    ADD_MEETUP,
    GET_ESTIMATED_COUNT,
    SUBSCRIBE_MEETUP,
    IWAS_HERE_MEETUP
} from '../constants/actions';
import { getCurrentLocation } from './location';
import { fetchUsuarios } from './usuarios';

export const addMeetup = (meetup) => {
    return {
        type: ADD_MEETUP, payload: meetup
    }
};

export const subscribeMeetup = (meetupName, username) => {
    return {
        type: SUBSCRIBE_MEETUP, payload: { meetupName, username }
    }
};
export const iWasHereMeetup = (meetupName, username) => {
    console.log("ACTION meetupName",meetupName)
    console.log("ACTION username",username)
    return {
        type: IWAS_HERE_MEETUP, payload: { meetupName, username }
    }
};

export const deleteMeetup = (meetup) => ({
    type: DELETE_MEETUP,
    payload: meetup.id
});

export const clearMeetup = () => ({
    type: CLEAR_MEETUP
});

export const getEstimatedCount = (meetup) => {
    return {
        type: GET_ESTIMATED_COUNT, payload: meetup
    }
};

export const mountedMeetupDashboardComponent = () => {
    return [
        { type: MEETUP_DASHBOARD_COMPONENT.MOUNTED },
        fetchUsuarios(),
        //getCurrentLocation(),
    ]
};
