import { FETCH_USUARIOS, LOGIN } from '../constants/actions';

const initialState = {
    byId: [],
    isFetching: false,
    username: "",
    token: "",
    rol: "",
    isLoggingIn: false
};

const usuarios = (state = initialState, action) => {

    switch (action.type) {
        case FETCH_USUARIOS: {
            const usuarios = action.payload;
            return { ...state, byId: usuarios, isFetching: true }
        }
        case LOGIN: {
            const nombreUsuario = action.payload.username;
            const rolUsuario = action.payload.rol;
            return { ...state, username: nombreUsuario, rol: rolUsuario, isLoggingIn: true };
        }
        case "LOGIN_FAIL": {
            return { ...state, username: "", isLoggingIn: false };
        }
        case "LOGOUT":
            return initialState
        default: { return state; }
    }
};

export default usuarios;