import React, { useState } from 'react';
import ListItem from '@material-ui/core/ListItem';
import Switch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import { getTheme } from '../selectors/ui';
import { changeTheme } from '../actions/ui';
import { logout } from '../actions/usuarios';
import { getUserName, getUserRol } from '../selectors/usuarios';
import { THEME } from '../constants/ui';
import { Typography } from '@material-ui/core';

const ProfileMenu = ({ theme, changeTheme, username, rol, logout }) => {

    const handleThemeChange = ({ target }) => {
        const theme = target.checked ? THEME.DARK : THEME.LIGHT;
        changeTheme(theme);
    }

    const handleOnClick = () => {
        logout();
    }

    return (
        <>
            <ListItem>
                <Switch
                    color="secondary"
                    checked={theme === THEME.DARK}
                    onChange={handleThemeChange}
                />
            </ListItem>
            <Typography variant="overline" display="block">
                {username} - {rol}
            </Typography>
            <Button color="inherit" onClick={handleOnClick}>Logout</Button>
        </>
    );
}

const mapStateToProps = state => ({
    theme: getTheme(state),
    username: getUserName(state),
    rol: getUserRol(state),
});

const mapDispatchToProps = {
    changeTheme,
    logout
};

export default connect(mapStateToProps, mapDispatchToProps)(ProfileMenu);
