import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { Button, Grid, Typography } from '@material-ui/core';
import ProfileMenu from './ProfileMenu';
import { CircularProgress, Fade } from '@material-ui/core';
import { getUiSpinner } from '../selectors/ui';
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
        marginBottom: theme.spacing(2)
    }
}));

const NavBar = ({ spinner }) => {

    const classes = useStyles();

    return (
        <>
            <AppBar position="static" className={classes.menuButton}>
                <Toolbar>
                    <Typography variant="h6">
                        Challenge
                     </Typography>
                    <ProfileMenu />
                    <Fade in={spinner} className={classes.spinner}>
                        <CircularProgress color="secondary" size="2em" />
                    </Fade>
                </Toolbar>
            </AppBar>
        </>
    );
}

const mapStateToProps = state => ({
    spinner: getUiSpinner(state)
})

export default connect(mapStateToProps, null)(NavBar);