import { api } from "../../actions/api";
import { createToast } from "../../actions/toast";
import { GET_CURRENT_LOCATION, GET_CURRENT_WEATHER } from "../../constants/actions";

const weatherMdl = ({ dispatch, getState }) => (next) => (
    action
) => {
    next(action);

    if (action.type === GET_CURRENT_LOCATION.SUCCESS) {
        const state = getState();

        const lat = state.location.lat;
        const lon = state.location.lon;
        
        dispatch(
            api.get(
                `https://cors-anywhere.herokuapp.com/https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&exclude=current,minutely,hourly,alerts&units=metric&appid=de35df86f22d1843233d0c35a0de233b`,
                [GET_CURRENT_WEATHER.PENDING],
                [GET_CURRENT_WEATHER.SUCCESS],
                [GET_CURRENT_WEATHER.ERROR, createToast("Error al consumir RESTAPI de clima. Verifique su conexión de internet")]
            )
        );
    }
};

export default weatherMdl;