
import './App.css';
import { connect } from 'react-redux';

import MainBody from './app/component/MainBaody';
import MeetupDashboard from './app/component/MeetupDashboard';
import NavBar from './app/component/NavBar';
import BlockScreen from './app/component/BlockScreen';
import Toaster from './app/component/Toaster';
import Login from './app/component/Login';
import React, { useEffect } from 'react';

import { getIsLoggedIn } from './app/selectors/usuarios';
import { getIsUiBlocked } from './app/selectors/ui';
import { mountedApp } from './app/actions/app';

const App = ({ isLoggedIn, isBlocked, mountedApp }) => {

  useEffect(() => {
    mountedApp();
  }, []);

  return (
    <div>
      {
        isLoggedIn ?
          <>
            <NavBar />
            <MainBody>
              <MeetupDashboard />
              <BlockScreen isBlocked={isBlocked} />
            </MainBody>
          </>
          :
          <Login />
      }
      <Toaster />
    </div>
  );
};

const mapStateToProps = state => ({
  isLoggedIn: getIsLoggedIn(state),
  isBlocked: getIsUiBlocked(state)
});

const mapDispatchToProps = {
  mountedApp,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);