export const getUiSlice = (state) => state.ui;
export const getUiSpinner = (state) => getUiSlice(state).requests > 0;
export const getTheme = (state) => getUiSlice(state).theme;
export const getIsSplitPopUpOpen = (state) => getUiSlice(state).splitPopUp;
export const getIsUiBlocked = (state) => getUiSlice(state).blocking > 0;


