import { GET_CURRENT_WEATHER } from "../constants/actions";

const initialState = {

};

const weather = (state = initialState, action) => {

    switch (action.type) {
        case GET_CURRENT_WEATHER.SUCCESS: {

            const weather = action.payload;
            weather.lastUpdatedTime = Date.now();
            weather.cityId = action.meta.id;
            weather.name = action.meta.name;

            return weather;

        }
        default: { return state; }
    }
};

export default weather;