const makeAsyncActionTypes = (type) => ({
  REQUEST: `${type}`,
  PENDING: `${type}_PENDING`,
  SUCCESS: `${type}_SUCCESS`,
  ERROR: `${type}_ERROR`,
});

const makeOpenClose = (name) => ({
  OPEN: `${name}_OPENED`,
  CLOSE: `${name}_CLOSED`,
});

const makeComponentActionTypes = (name) => ({
  MOUNTED: `${name}_MOUNTED`,
  RENDERED: `${name}_RENDERED`,
  UMOUNTED: `${name}_UMOUNTED`,
});

