import { FETCH_USUARIOS, LOGIN } from '../constants/actions';
import usuarios from '../mocks/usuarios.json';
import { createToast } from './toast';

export const fetchUsuarios = () => {
    return { type: FETCH_USUARIOS, payload: usuarios }
};

export const login = (username) => {

    const u = usuarios?.find(usu => usu.username == username);

    if (u === undefined) {
        return [{
            type: "LOGIN_FAIL", payload: null
        }, createToast("Usuario no registrado")]
    }
    else {
        return {
            type: LOGIN, payload: u
        }
    }
};

export const logout = () => ({
    type: "LOGOUT"
});